body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  box-sizing: border-box;
  border: 0;
  color: white;
  font-family: 'Exo 2', sans-serif;
  margin: 0;
  padding: 0;
}

body{
  background: rgb(20,20,20);
  background: linear-gradient(180deg, rgba(20,20,20,1) 0%, rgba(0,0,0,1) 100%);
  height: 100%;
  min-height: 100vh;
}

main{
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  width: 80vw;
}

input{
  border-radius: 15px;
  color: black;
  margin: 15px auto;
  min-width: 300px;
  padding: 20px;
  text-align: center;
}

button{
  background-color: orange;
  border-radius: 15px;
  margin: 15px;
  padding: 20px;
}

.header{
  margin: 15px 0 30px 0;
  text-align: left;
}

.stats{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.stats__stat{
    margin: 10px 0;
    width: 50%;
}

.stats__stat h2{
  font-size: 1rem;
  display: inline-block;
}

.stats__stat p{
  display: inline-block;
  font-size: 3rem;
  padding: 10px;
}

.stats__pic{
  width: 30%;
  border-radius: 50%;
}

.placeholder-img{
  /*min-height: 500px;*/
  object-fit: cover;
  object-position: center;
  width: 90%;
}

.placeholder-text{
  font-size: 1rem;
  font-weight: lighter;
  margin: 20px;
}

.loading-sign{
  display: block;
  margin: 0 auto;
  width: 25%;
}

@media screen and (max-width: 600px){

  main{
    overflow-x: hidden;
    width: 100vw;
  }

  .stats__pic{
    border-radius: 0;
    max-height: 250px;
    object-fit: cover;
    object-position: center;
    width: 100%;
  }
}
